<template>
  <div>
    <el-radio-group v-model="chainId">
      <el-radio value="btc:mainnet">btc:mainnet</el-radio>
      <el-radio value="btc:testnet">btc:testnet</el-radio>
      <el-radio value="btc:signnet">btc:signnet</el-radio>
      <el-radio value="fractal:mainnet">fractal:mainnet</el-radio>
    </el-radio-group>
    <div class="line"></div>
    <el-row :gutter="12">
      <el-col :span="24">
        <el-button class="grid-content" type="warning" @click="btc_signMessage()">signMessage</el-button>
      </el-col>
      <div class="line"></div>
      <el-col :span="24">
        <div style="font-size: 12px; color: gray;">(Patternlegacy)</div>
        <el-input class="grid-content" v-model="sendAmount" clearable placeholder="" />
        <el-button class="grid-content" type="warning" @click="btc_send()">send</el-button>
        <br>
        <el-input class="grid-content" v-model="sendSatoshisAmount" clearable placeholder="satoshi" />
        <el-button class="grid-content" type="warning" @click="btc_sendBitcoin()">sendBitcoin</el-button>
      </el-col>
      <div class="line"></div>
      <el-col :span="24">
        <el-input class="grid-content" v-model="psbtHex" clearable placeholder="Psbt" />
        <el-button class="grid-content" type="warning" @click="btc_signPsbt_up()">signPsbt(NFT)</el-button>
        <br>
        <el-button class="grid-content" type="warning"
          @click="btc_signAndPushPsbt_up()">signAndPushPsbt(NFT)</el-button>
        <br>
        <div style="font-size: 12px;margin-top: 12px; margin-left: 4px; color: gray; text-align: left;">
          <li>
            1. OKX AppBtc NFT
          </li>
          <li>
            2. 
          </li>
          <li>
            3. psbt
          </li>
          <li>
            4.  https://chainquery.com/bitcoin-cli/decodepsbt
          </li>
        </div>
      </el-col>
      <div class="line"></div>
      <el-col :span="24">
        <el-input class="grid-content" v-model="nftId" clearable placeholder="NFT ID" />
        <el-input class="grid-content" v-model="receiver" clearable placeholder="" />
        <el-button class="grid-content" type="warning" @click="btc_sendInscription()">sendInscription</el-button>
        <div style="font-size: 12px;margin-top: 12px; margin-left: 4px; color: gray; text-align: left;">
          <li>
            1. webNFTBtc NFT
          </li>
          <li>
            2. NFTURLNFT idurl
          </li>
          <li>
            3. sendInscription
          </li>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { ref } from 'vue';

export default {
  name: 'AllBtcTransactionPanel',
  props: {
    msg: String
  },
  setup() {
    const nftId = ref('')
    const sendAmount = ref(0.000015)
    const sendSatoshisAmount = ref(17100)
    const psbtHex = ref('')
    const receiver = ref('')
    const chainId = ref('btc:mainnet')
    return {
      nftId,
      receiver,
      psbtHex,
      sendAmount,
      sendSatoshisAmount,
      chainId
    }
  },
  methods: {
    btc_signMessage() {
      let params = {
        from: "bc1p7pgnqe87red4cvd7ml6rh9pl9ufpr522k2y3dpeyrvfc6g2g3r3s3ae9dr",
        signStr: `Welcome to BTC(${this.chainId})`
      };
      this.$emit('generateTransactionParams', this.chainId, params, "btc_signMessage")
    },

    btc_send() {
      if (this.sendAmount == 0) {
        alert("")
        return
      }
      let params = {
        from: '',
        to: '1NKnZ3uAuQLnmExA3WY44u1efwCgTiAxBn', // patternlegacy
        value: `${this.sendAmount}`
      }
      this.$emit('generateTransactionParams', this.chainId, params, "btc_send")
    },

    btc_sendBitcoin() {
      if (this.sendAmount == 0) {
        alert("")
        return
      }

      let params = {
        from: '',
        toAddress: '1NKnZ3uAuQLnmExA3WY44u1efwCgTiAxBn', // patternlegacy
        satoshis: `${this.sendSatoshisAmount}`,
        options: {
          feeRate: 16
        }
      }
      this.$emit('generateTransactionParams', this.chainId, params, "btc_sendBitcoin")
    },

    btc_signPsbt_up() {
      if (!this.psbtHex) {
        alert("psbt")
        return
      }
      this.$emit('generateTransactionParams', this.chainId, {
        psbtHex: this.psbtHex,
        options: {
          autoFinalized: false
        }
      }, "btc_signPsbt")
    },

    btc_signAndPushPsbt_up() {
      if (!this.psbtHex) {
        alert("psbt")
        return
      }
      this.$emit('generateTransactionParams', this.chainId, {
        psbtHex: this.psbtHex,
        options: {
          autoFinalized: true
        }
      }, "btc_signAndPushPsbt")
    },

    btc_sendInscription() {
      if (!this.nftId) {
        alert("NFT ID")
        return
      }

      if (!this.receiver) {
        alert("")
        return
      }

      this.$emit('generateTransactionParams', this.chainId, {
        address: this.receiver,
        inscriptionId: this.nftId,
        options: {
          feeRate: "15"
        }
      }, "btc_sendInscription")
    }
  }
}
</script>

